@import url("https://fonts.googleapis.com/css2?family=Work+Sans:ital,wght@0,100..900;1,100..900&display=swap");
:root {
  --background: rgb(111, 111, 232);
  --tipo-principal: "Work Sans", sans-serif;
}

@media (prefers-reduced-motion: reduce) {
  * {
    animation: none !important;
    transition: none !important;
  }
}
* {
  margin: 0;
  padding: 0;
  min-width: 0;
  border: 0;
  box-sizing: border-box;
  vertical-align: baseline;
}

body {
  min-height: 100dvh;
  font-family: "Work Sans", sans-serif;
}
body.hidden {
  overflow: hidden;
}

#page {
  overflow: hidden;
}

h1,
h2,
h3 {
  text-wrap: balance;
}

p {
  text-wrap: pretty;
}

img,
picture,
video,
iframe,
figure {
  max-width: 100%;
  width: 100%;
  display: block;
  margin: 0;
  object-fit: cover;
  object-position: center center;
}

figure img {
  height: 100%;
}

a {
  display: block;
  text-decoration: none;
  color: inherit;
  font-size: inherit;
}

a:visited {
  color: inherit;
}

p a {
  display: inline;
}

ul {
  margin: 0;
}

li {
  list-style-type: none;
}

html {
  scroll-behavior: smooth;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
span,
a,
strong,
blockquote,
i,
b,
u,
em {
  font-size: 1em;
  font-weight: inherit;
  font-style: inherit;
  text-decoration: none;
  color: inherit;
}

strong {
  font-weight: bold;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

::selection {
  background-color: var(--background);
  color: white;
}

form,
input,
textarea,
select,
button,
label {
  font-family: inherit;
  font-size: inherit;
  background-color: transparent;
  color: inherit;
  display: block;
  appearance: none;
  outline: none;
}

table,
tr,
td {
  border-collapse: collapse;
  border-spacing: 0;
}

svg {
  width: 100%;
  display: block;
  fill: currentColor;
}

body {
  min-height: 100vh;
  font-size: 100%;
  font-family: var(--tipo-principal);
  line-height: 1.4em;
  font-smooth: always;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.max-width {
  box-sizing: border-box;
  max-width: 2000px;
  margin: 0 auto;
}
@media (max-width: 2300px) {
  .max-width {
    max-width: 1500px;
  }
}
@media (max-width: 1800px) {
  .max-width {
    max-width: calc(100vw - 240px);
  }
}
@media (max-width: 1440px) {
  .max-width {
    max-width: calc(100vw - 190px);
  }
}
@media (max-width: 992px) {
  .max-width {
    max-width: calc(100vw - 80px);
  }
}
@media (max-width: 480px) {
  .max-width {
    max-width: 100vw;
    padding: 0;
  }
}
@media (min-width: 320px) and (max-width: 480px) {
  .max-width {
    max-width: 100vw;
    padding: 0 20px;
  }
}

main {
  min-height: 45vh;
  overflow: hidden;
}

.p {
  font-size: 18px;
  font-weight: 400;
  color: white;
}
.p b {
  font-weight: 700;
}

.button {
