}

.burger {
  position: relative;
  width: 40px;
  height: 30px;
  background: transparent;
  cursor: pointer;
  display: block;
}

.burger input {
  display: none;
}

.burger span {
  display: block;
  position: absolute;
  height: 4px;
  width: 100%;
  background: white;
  border-radius: 9px;
  opacity: 1;
  left: 0;
  transform: rotate(0deg);
  transition: 0.25s ease-in-out;
}

.burger span:nth-of-type(1) {
  top: 0px;
  transform-origin: left center;
}

.burger span:nth-of-type(2) {
  top: 50%;
  transform: translateY(-50%);
  transform-origin: left center;
}

.burger span:nth-of-type(3) {
  top: 100%;
  transform-origin: left center;
  transform: translateY(-100%);
}

.burger input:checked ~ span:nth-of-type(1) {
  transform: rotate(45deg);
  top: 0px;
  left: 5px;
}

.burger input:checked ~ span:nth-of-type(2) {
  width: 0%;
  opacity: 0;
}

.burger input:checked ~ span:nth-of-type(3) {
  transform: rotate(-45deg);
  top: 28px;
  left: 5px;
}

.tabs-products__loader {
  min-height: 510px;
  display: grid;
  place-items: center;
}

.spinner-container {
  width: 150px;
  height: 150px;
  position: relative;
  margin: 30px auto;
  overflow: hidden;
}

.spinner {
  position: absolute;
  width: calc(100% - 9.9px);
  height: calc(100% - 9.9px);
  border: 5px solid transparent;
  border-radius: 50%;
  border-top-color: #89759A;
  animation: spin 5s cubic-bezier(0.17, 0.49, 0.96, 0.79) infinite;
}

.shortcode-grid {
  padding: 40px 0;
}
@media screen and (min-width: 768px) {
  .shortcode-grid {
    padding: 60px 0;
  }
}
@media screen and (min-width: 1800px) {
  .shortcode-grid {
    padding: 70px 0;
  }
}
.shortcode-grid__h2 {
  margin-bottom: 30px;
  text-align: center;
}
.shortcode-grid .swiper-product-replic {
  overflow: hidden;
}
.shortcode-grid .swiper-slide {
  border-radius: 15px;
  overflow: hidden;
}

.card-product__flag--discount-percentage, .card-product__flag--new {
  height: 40px;
  width: 100px;
  position: absolute;
  z-index: 1;
  left: -36px;
  top: 0;
  padding: 0px 16px;
  display: flex;
  justify-content: center;
  align-items: end;
  font-size: 10px;
  font-weight: 600;
  background-color: #FF004F;
  color: white;
  rotate: -48deg;
}
.card-product__flag--discount-percentage {
  background-color: #AC889F;
}
.card-product__card {
  height: 420px;
  padding: 10px;
  border-radius: 15px;
  overflow: hidden;
  border: 1px solid #FBE2E9;
  background: #FBFBFB;
  box-shadow: 1px 1px 5px 0px rgba(0, 0, 0, 0.1);
}
.card-product__card:hover img {
  scale: 1.1;
}
.card-product__card:hover .card-product__buttons {
  bottom: 10px;
  z-index: 10;
  opacity: 1;
}
.card-product__thumbnail {
  position: relative;
  height: 300px;
  width: 100%;
  overflow: hidden;
  border-radius: 10px;
}
.card-product__thumbnail img {
  height: 100%;
  object-fit: cover;
  transition: all 0.3s ease;
}
.card-product__figure {
  height: 100%;
}
.card-product__buttons {
  position: absolute;
  bottom: -60px;
  width: 100%;
  display: flex;
  justify-content: center;
  gap: 30px;
  opacity: 0;
  transition: all 0.3s ease;
}
@media screen and (max-width: 768px) {
  .card-product__buttons {
    bottom: 10px;
    z-index: 10;
    opacity: 1;
  }
}
.card-product__tooltip {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  padding: 0.3em 0.6em;
  opacity: 0;
  pointer-events: none;
  transition: all 0.4s cubic-bezier(0.23, 1, 0.32, 1);
  background: #AC889F;
  z-index: -1;
  border-radius: 8px;
  scale: 0;
  transform-origin: 0 0;
  text-transform: capitalize;
  font-weight: 400;
  font-size: 16px;
  box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;
}
.card-product__tooltip::before {
  position: absolute;
  content: "";
  height: 0.6em;
  width: 0.6em;
  bottom: -0.2em;
  left: 50%;
  transform: translate(-50%) rotate(45deg);
  background: #AC889F;
}
.card-product__add-cart {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.889);
  cursor: pointer;
  transition: all 0.5s ease;
}
.card-product__add-cart svg {
  fill: transparent;
}
.card-product__add-cart:hover {
  scale: 1.1;
}
.card-product__add-cart:hover .card-product__tooltip {
  top: -35%;
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
  scale: 1;
  font-size: 11px;
  width: max-content;
  color: white;
  animation: shake 0.5s ease-in-out both;
}
.card-product__add-cart:active {
  scale: 0.9;
}
.card-product__info {
  margin-top: 20px;
  text-align: center;
  font-weight: 700;
}
.card-product__h3 {
  color: #89759A;
  text-transform: uppercase;
}
.card-product__price {
  font-weight: 600;
  color: #8c8c8c;
}

.aside-cart {
