.filter {
  margin-top: 50px;
  overflow: hidden;
}
.filter__button-movil {
  position: fixed;
  right: 10px;
  z-index: 100;
  width: 50px;
  height: 50px;
  display: grid;
  place-items: center;
  border-radius: 50%;
  background: linear-gradient(145deg, #cacaca, #f0f0f0);
  box-shadow: 20px 20px 60px #bebebe, -20px -20px 60px #ffffff;
  cursor: pointer;
}
.filter__button-movil svg {
  width: 20px;
  height: 20px;
  fill: transparent;
}
.filter__button-movil--close {
  display: none;
}
.filter__button-movil--categories {
  left: 10px;
}
@media screen and (min-width: 768px) {
  .filter__button-movil {
    display: none;
  }
}
@media screen and (min-width: 768px) {
  .filter__wrapper {
    display: grid;
    grid-template-columns: 1fr 3fr;
  }
}
@media screen and (max-width: 768px) {
  .filter__filter {
    position: fixed;
    left: -100%;
    top: 0;
    width: 100%;
    height: 100%;
    transition: all 0.3s ease;
    z-index: 910;
  }
  .filter__filter::after {
    content: "";
    position: absolute;
    z-index: 1;
    right: 0;
    top: 0;
    width: 50%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.421);
    z-index: -10;
  }
  .filter__filter .filter__aside {
    position: relative;
    z-index: 1;
    width: 50%;
    height: 100%;
    padding-top: 100px;
    background-color: white;
  }
  .filter__filter .filter__aside .filter__button-movil--close {
    position: absolute;
    right: -25px;
    top: 50px;
    display: grid;
  }
  .filter__filter .filter__h2 {
    padding-left: 30px;
  }
  .filter__filter.open {
    left: 0;
  }
}
.filter__h2 {
  margin-bottom: 20px;
