}
.filter__button a {
  width: 100%;
  height: 100%;
  text-align: start;
}
@media screen and (max-width: 768px) {
  .filter__filters {
    position: fixed;
    right: 0%;
    top: 0;
    width: 0%;
    overflow: hidden;
    height: 100%;
    transition: all 0.3s ease;
    z-index: 910;
    display: flex;
    justify-content: end;
  }
  .filter__filters::after {
    content: "";
    position: absolute;
    z-index: 1;
    left: 0;
    top: 0;
    width: 50%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.421);
    z-index: -10;
  }
  .filter__filters.open {
    right: 0;
    width: 100%;
  }
  .filter__filters .filter__filters--aside {
    position: relative;
    z-index: 1;
    width: 50%;
    height: 100%;
    padding-top: 100px;
    background-color: white;
  }
  .filter__filters .filter__filters--aside .filter__button-movil--close {
    position: absolute;
    left: -25px;
    top: 50px;
    display: grid;
  }
  .filter__filters .filter__filters--button {
    border-right: none;
  }
  .filter__filters .filter__filters--terms {
