}
.filter__filters--button {
  padding: 16px 24px;
  height: 100%;
  display: flex;
  align-items: center;
  text-transform: capitalize;
  cursor: pointer;
  border-right: 1px solid #8c8c8c;
}
.filter__filters--button svg {
  height: 18px;
}
.filter__filters--button path {
  fill: transparent;
}
.filter__filters--attribute {
  position: relative;
}
.filter__filters--attribute:hover .filter__filters--terms {
  z-index: 10;
