  font-size: clamp(12px, 1.2vw, 14px);
  color: green;
}
.single-product .variations {
  margin-top: 20px;
}
.single-product .variations tr {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  text-transform: capitalize;
}
.single-product .variations .reset_variations {
  margin: 10px 0;
  padding: 0px 10px;
  font-size: 14px;
  width: max-content;
  border: 1px solid black;
}
.single-product .variations select {
  padding-bottom: 5px;
  text-transform: capitalize;
  padding-left: 15px;
  border-bottom: 1px solid rgb(181, 181, 181);
  cursor: pointer;
}
.single-product .cart,
.single-product .woocommerce-variation-add-to-cart {
  display: flex;
  align-items: center;
  gap: 10px;
}
.single-product .qty {
  height: 2.23rem;
  font-weight: 700;
  background: #f3f3f3;
  border-radius: 4px;
}

.more-info {
  padding: 50px 0;
}
@media screen and (min-width: 768px) {
  .more-info {
    padding: 70px 0;
  }
}
.more-info__h3 {
  color: #89759A;
  font-size: 18px;
  line-height: normal;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 7.6px;
}
@media screen and (min-width: 768px) {
  .more-info__h3 {
    font-size: 22px;
  }
}
@media screen and (min-width: 1024px) {
  .more-info__h3 {
    font-size: 26px;
  }
}
@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes shake {
  0% {
    rotate: 0;
  }
  25% {
    rotate: 7deg;
  }
  50% {
    rotate: -7deg;
  }
  75% {
    rotate: 1deg;
  }
  100% {
    rotate: 0;
  }
}